html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  background: #FFF;
  font-size: 16px;
  line-height: 1.4;
}

html, body {
  height: 100%;
  width: 100%;
}

body {
  font-size: 16px;
}

.wrapper {
  height: 100%;
  overflow: hidden;
}

.out {
  height: 100%;
  width: 100%;
}

.maincontent {
  position: relative;
  height: 100%;
  width: 100%;
  top: 0;
  transition: top 1s;
}

.page {
  height: 100%;
  width: 100%;
  position: relative;
}

.page__container {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 16px;
  text-align: center;
  transform: translate(-50%, -50%);
}

.link.active {
  color: red;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.full-section {
  min-height: 100vh;
  padding: 30px 0;
}

.fp-right {
  position: fixed;
  top: 50%;
  right: 25px;
  z-index: 100;
  margin-top: -32px;
  font-size: 14px;
}

.fp-right ul li {
  display: block;
  width: 14px;
  height: 13px;
  margin: 20px;
  position: relative;
}

.fp-right ul li a {
  display: block;
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
  cursor: pointer;
  text-decoration: none;
}

.fp-right ul li a span {
  border-radius: 50%;
  position: absolute;
  z-index: 1;
  height: 4px;
  width: 4px;
  border: 0;
  background: #fff;
  left: 50%;
  top: 50%;
  margin: -2px 0 0 -2px;
  transition: all .1s ease-in-out;
}

.fp-right ul li a.active span {
  height: 12px;
  width: 12px;
  margin: -6px 0 0 -6px;
  border-radius: 100%;
}

.fp-right ul li:hover a span {
  width: 10px;
  height: 10px;
  margin: -5px 0 0 -5px;
}

.fp-right .fp-tooltip.right {
  display: none;
}

.nav-top {
  z-index: 1;
  position: absolute;
}

.nav-top ul {
  margin: 0;
  width: 100%;
  background: #fff;
  list-style-type: none;
  position: fixed;
  top: 0;
  left: 0;
  text-align: center;
}

.nav-top li {
  display: inline-block;
  padding: 15px;
}
